.demo-switcher {
  position: fixed !important;
  right: 30px;
  bottom: 30px;
  z-index: 999999;
}

.demo-options {
  z-index: 900;
  position: fixed;
  bottom: 80px;
  max-width: 365px;
  border: 0;
  box-shadow: none;
  padding: 0;
  border-radius: 3px;
  right: 30px;
  background: #fff;
  transform: scale(0);
  transform-origin: bottom right;
  transition: transform 0.15s ease;
  box-shadow: 0 1.5px 4px rgba(0, 0, 0, 0.24), 0 1.5px 6px rgba(0, 0, 0, 0.12);
}

@media (max-height: 800px) {
  .demo-options {
    height: 600px;
  }
}

@media (max-width: 480px) {
  .demo-options {
    max-width: 270px;
    height: 300px;
  }
}

@media (max-height: 480px) {
  .demo-options {
    height: 220px;
  }
}

.demo-options.mCS-autoHide > .mCustomScrollBox > .mCSB_scrollTools, .demo-options.mCS-autoHide > .mCustomScrollBox ~ .mCSB_scrollTools {
  opacity: 1;
}

.demo-options.active {
  transform: scale(1);
}

.demo-options .demo-body {
  padding: 20px;
  background-color: #fff;
  border-left: 1px solid #eeeeee;
  color: #616161;
}

